@import url('https://fonts.googleapis.com/css2?&family=Noto+Sans+KR:wght@400;500&family=Archivo&family=Cinzel+Decorative:wght@400;700;900&display=swap');

/* style reset */
* {
	margin:0;
	padding:0;
	outline:none;
	box-sizing:border-box;
	text-size-adjust:none;    
	-webkit-overflow-scrolling:touch;
}
html {
	width:100%; 
	height:100%;
}
body {
	width:100%; 
	min-width:1200px;
	height:100%; 
	color:#666; 
	font-size:15px;
	word-wrap: break-word;
	word-break:keep-all;
	background-color:#fbf8f8;
	border:20px solid #ebebeb
} 
ul, ol, li {
	list-style:none;
}
a {
	text-decoration:none;
	color:inherit;
	cursor:pointer;
}
em, address, i {
	font-style:normal;
}
strong, em, b {
	font-weight:800;
}
caption {
	visibility:hidden;
	overflow:hidden;
	width:0;
	height:0;    
	font-size:0;
}
hr {
	display:none;
}
pre {
	white-space:pre-line;
	word-break:break-all;
}
img {
	border:0;
}
button {
	background:none;
	border:0;
	vertical-align:middle;
	cursor:pointer;
}

img, input, select, textarea {
	vertical-align:middle
}

body, button, input, textarea, select {
	font-family: 'Archivo', 'Noto Sans KR', sans-serif, serif, Dotum, Arial,;
}

#root{
	width:100%;
	height:100%;
}

$breakpoints: (
  'mobile': (max-width: 960px),
  'tablet': (max-width:1200px),
);


@mixin mq($name) {
  $raw-query: map-get($breakpoints, $name);

  @if $raw-query {
	$query: if(
	  type-of($raw-query) == 'string',
	  unquote($raw-query),
	  inspect($raw-query)
	);

	@media #{$query} {
	  @content;
	}
  } @else {
	@error 'No value found for `#{$name}`. '
		 + 'Please make sure it is defined in `$breakpoints` map.';
  }
}

@mixin fontSize($w, $tablet, $m){
	font-size:$w + px;
	@include mq(tablet){
		font-size:($tablet * 100) / 1200 + vw;
	}
	@include mq(mobile){
		font-size:$m + px;
	}
}