@charset "utf-8";

@import '_reset';
@import '_main';
@import '_work';


header{
	position: relative;
	max-width:1000px;
	margin:0 auto;
	z-index:6;
	width:100%;
	padding-top:213px;
	text-align:center;
	transition:1s;

	h1{
		position: absolute;
		left:250px;
		top:100px;
		display:inline-block;
		font-family: 'Cinzel Decorative', cursive;
		color:#69594c;
		font-size:50px;
		font-weight:400;
		text-align:center;
		transition:1.4s ease;
		strong{
			font-weight:700;
		}
	}
	p{
		position: absolute;
		left:450px;
		top:170px;
		font-size:14px;
		transition:1.4s ease;
	}
	ul{
		position: absolute;
		right:330px;
		top:260px;
		opacity: 0;
		transition:1s;
		text-align:center;

		li{
			display:inline-block;
			a, > div{
				display:inline-block;
				min-width:150px;
				padding:7px;
				margin:0 10px;
				color:#fff;
				font-size:12px;
				cursor: pointer;
				border-radius:20px;
				background-color:#b2a79e;
			}
		}
	}
	&.navigation{
		h1{
			top:40px;
		}
		p{
			top:110px;
			transition-delay:0.3s;
		}
		ul{
			top:190px;
			transition-delay:1.2s;
			opacity:1;
		}
	}
	&.isSub{
		max-width:1170px;
		padding-top:115px;
		h1{
			left:0;
			top:30px;
			font-size:40px;
		}
		p{
			top:50px;
			left:430px;
			transition-delay:0s;
		}
		ul{
			top:45px;
			right:0;
			transition-delay:0s;
		}
	}
}
